import { useEffect, useState } from 'react';
import './App.css';

const App = () => {

  const [walks, setWalks] = useState([]);

  const getWalks = ({access_token}) => {
    const activities_link = `https://www.strava.com/api/v3/athlete/activities?access_token=${access_token}`
    fetch(activities_link)
        .then(response => response.json())
        .then(data => {
          const filteredWalks = data.filter(walk => {
            return walk.type === 'Walk'
          })
          .filter(walk => {
            const walkDate = new Date(walk.start_date);
            if (walkDate.getDate() >= 6 && walkDate.getDate() <= 12 && walkDate.getMonth() === 6) {
              return true;
            }
            return false;
          })
          setWalks(filteredWalks);
          console.log(filteredWalks);
        });
  }

  useEffect(() => {
    fetch('https://www.strava.com/oauth/token', {
      method: 'POST',
      headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        client_id: '68150',
        client_secret: '409d4f9261a2a0ab5410113d98ab6abe682cba00',
        refresh_token: '2d209a528d63f336d2205629b0d4f152c766d6bc',
        grant_type: 'refresh_token'
        // below code to get activities:read_all
        // code: '8a59707fb0c605e201375e2ba63da0bcb6fa0329',
        // grant_type: 'authorization_code'
    })
    })
    .then(response => response.json())
    .then(data => {
      getWalks(data);
    })
  }, []);

  const walkedTotal = walks.reduce((a, b) => {
    return a + b.distance;
  }, 0)

  return (
    <div className="App">
      <h2>70km walking per week progress</h2>
      <ul>
        <li>
          <span>Tue 06:</span> 
          <b>{walks[0] ? (walks[0].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Wed 07:</span> 
          <b>{walks[1] ? (walks[1].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Thu 08:</span> 
          <b>{walks[2] ? (walks[2].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Fri 09:</span> 
          <b>{walks[3] ? (walks[3].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Sat 10:</span> 
          <b>{walks[4] ? (walks[4].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Sun 11:</span> 
          <b>{walks[5] ? (walks[5].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
        <li>
          <span>Mon 12:</span> 
          <b>{walks[6] ? (walks[6].distance / 1000).toFixed(2) : '----- '}km</b>
        </li>
      </ul>
      <h3>Left to go: {70 - (walkedTotal / 1000).toFixed(2)}km</h3>
    </div>
  );
}

export default App;
